import React from 'react';
import { useRecoilState } from 'recoil';
import Box from '../../../../../../../components/layout/Box';
import InfoLabelSM from '../../../../../../../components/UI/Atoms/Info/InfoLabelSM';
import InfoEditInput from '../../../../../../../components/UI/Atoms/Input/InfoEditInput';
import { registDataAtom } from '../../../recoil/atom/registDataAtom';
import RegInfo from './RegInfo';
import BasicTextArea from '../../../../../../../components/UI/Atoms/Textarea/BasicTextArea';
import AdornmentInput from '../../../../../../../components/UI/Atoms/Input/AdornmentInput';
import { numberMaxLength2 } from '../../../../../../../utils/render/numberMaxLength';
import SelectInput from '../../../../../../../components/common/Select/SelectInput';

const StepD = () => {
  const [reg, setReg] = useRecoilState(registDataAtom);

  // handler
  const handleChange = (e) => {
    const { name, value } = e.target;
    setReg({ ...reg, [name]: value });
  };


  return (
    <>
      <RegInfo />
      <Box>
        <div className="flex flex-col gap-[10px] px-[86px]">
          <div className="flex justify-between gap-[40px] items-center">
            <div className="flex flex-1 items-center">
              <InfoLabelSM></InfoLabelSM>
              <AdornmentInput
                unit=""
                placeholder="1 ~ 31"
                type="number"
                min="1"
                max="31"
                name="due_date"
                value={reg.due_date}
                onChange={handleChange}
                onInput={numberMaxLength2}
              />
            </div>
            <div className="flex flex-1 items-center">
              <InfoLabelSM></InfoLabelSM>
              <AdornmentInput
                unit=""
                placeholder="0"
                name="paying_amount"
                value={reg.paying_amount}
                onChange={handleChange}
              />
            </div>
          </div>
          <div className="flex justify-between gap-[40px] items-center">
            <div className="flex flex-1 items-center">
              <InfoLabelSM></InfoLabelSM>
              <SelectInput
                name="payment_method"
                value={reg.payment_method || ''}
                onChange={handleChange}
                placeholder="// "
                payment
                className="text-[14px]"
              />
            </div>
            <div className="flex flex-1 items-center">
              <InfoLabelSM></InfoLabelSM>
              <InfoEditInput
                placeholder="   "
                name="depositor_name"
                value={reg.depositor_name}
                onChange={handleChange}
              />
            </div>
          </div>
          <div className="flex flex-1 items-center mb-[20px]">
            <InfoLabelSM>
              
              <br />
              
            </InfoLabelSM>
            <BasicTextArea
              placeholder="    ."
              length={520}
              name="payment_note"
              value={reg.payment_note}
              onChange={handleChange}
            />
          </div>
        </div>
      </Box>
    </>
  );
};

export default StepD;
